import common from '../../common/common.js';
var _this;
export default {
	data() {
		return {
			isLogin: false,
			nickname: ''
		};
	},
	methods: {
		login() {
			uni.navigateTo({
				url: './login/login'
			});
		},
		jumpToCollcotion() {
			let info = this.$store.state.login;
			if (info.length == 0) {
				uni.showModal({
					title:"提示",
					content:"您还未登录，请登录后重试～",
					success: (res) => {
						if(res.confirm){
							uni.navigateTo({
								url:'../user/login/login'
							})
						}
					}
				})
			} else {
				uni.navigateTo({
					url: './collect/collect'
				});
			}
		},
		jumpToAbout() {
			uni.navigateTo({
				url: './about/about'
			});
		},
		logout() {
			uni.showModal({
				title: '系统提示',
				content: '是否需要退出登录？',
				success: res => {
					if (res.confirm) {
						uni.setStorageSync(common.user_login, '');
						this.isLogin = false;
						this.nickname = null;
						this.$store.state.login = '';
					} else if (res.cancel) {
					}
				}
			});
		}
	},
	onLoad() {},
	onShow() {
		let info = this.$store.state.login;
		console.log(info);
		if (info.length > 0) {
			this.isLogin = true;
			this.nickname = JSON.parse(info).nickname;
		}
	}
};
